<template>
    <div class="order-list">
		<div class="list-content" v-if="orderList.length > 0">
			<div class="list-main" v-for="item in orderList">
			    <div class="list-msg">
			        <span class="inner-span">{{ $L('订单编号')+'：' + item.sn }}</span>
			        <span class="order-time">{{ $L('下单时间')+'：' + item.addTime }}</span>
			    </div>
			    <div class="list-head">
			        <div class="item item-two">{{ $L('产品信息') }}</div>
			        <div class="item item-one">{{ $L('价格') }}</div>
			        <div class="item item-one">{{ $L('实付金额') }}</div>
			        <div class="item item-one">{{ $L('订单状态') }}</div>
			        <div class="item item-three">{{ $L('操作') }}</div>
			    </div>
			    <div class="list-info">
			        <div class="item item-two" @click="changeOrderType(item,'info')">
			            <div class="info" v-for="goods in item.orderGoods">
			                <div class="list-main-left">
			                    <el-image class="goods-img" :src="goods.productPic" fit="contain">
			                        <div slot="error">
			                            <span class="bbx-font bbx-icon-tupianjiazaishibai error-img mainFontColor"></span>
			                        </div>
			                    </el-image>
			                </div>
			                <div class="list-main-right">
			                    <p class="list-main-right-item orderInfo mainFontColorHover" @click="gotoGoodsPage(goods)">
			                        {{goods.chrName}}<span v-if="item.mark" class="mainFontColor"> 【{{item.mark}}】 </span>
			                    </p>
			                    <p class="list-main-right-item gray" v-html="goods.goodsSpec"></p>
			                    <p class="list-main-right-item gray">×{{goods.goodsNum}}</p>
			                </div>
			            </div>
			        </div>
			        <div class="item item-one">
			            <div class="price font-price" v-for="goods in item.orderGoods">{{ goods.goodsPrice }}<br
			                    v-if="goods.hasOwnProperty('sellPrice')"/><span
			                    class="old-price" v-if="goods.hasOwnProperty('sellPrice')">{{ goods.sellPrice }}</span>
			            </div>
			        </div>
			        <div class="item item-one">
			            <div class="price font-red">{{ item.orderAmount }}<br><span class="font-grey">{{ item.payName }}</span></div>
			        </div>
			        <div class="item item-one">
			            <div v-if="item.thisOrderStatus < 0"
			                 class="color-info item-one-txt">{{$L('已关闭')}}
			            </div>
			            <div v-else-if="item.thisOrderStatus === 0 || item.thisOrderStatus === 1" class="color-danger item-one-txt">
			                {{statusFunc(item.payStatus)}}
			            </div>
			            <div v-else-if="item.thisOrderStatus === 2"
			                 class="color-warning item-one-txt">
			                {{$L('待收货')}}
			            </div>
			            <div v-else-if="item.thisOrderStatus === 3" class="color-warning item-one-txt">
			                {{$L('退货中')}}
			            </div>
						<div v-else-if="item.thisOrderStatus === 6" class="color-warning item-one-txt">
							{{$L('待发货')}}
						</div>
			            <div v-else-if="item.thisOrderStatus === 4" class="color-success item-one-txt">
			                {{$L('已退货')}}
			            </div>

			            <div class="color-success item-one-txt" v-else>
			                {{$L('已完成')}}
			            </div>
			        </div>
			        <div class="item item-three">
			            <el-button v-if="item.commentsStatus === 0"
			                       size="small"
			                       type="primary"
			                       class="item-btn"
			                       @click="changeOrderType(item,'evaluate')">{{$L('去评价')}}</el-button>
			            <el-button
			                    v-if="item.hasOwnProperty('canRefund') && item.canRefund === 1"
			                    @click="changeOrderType(item,'refund')"
			                    class="item-btn"
			                    size="small">{{$L('申请退款')}}</el-button>
			
			            <el-button v-if="(item.thisOrderStatus === 0 || item.thisOrderStatus === 1) && item.payStatus>=0" type="primary"
			                       size="small"
			                       class="item-btn"
			                       @click="changeOrderType(item,'pay')">{{$L('立即付款')}}</el-button>
			
			            <el-button v-if="item.thisOrderStatus === 2"
			                       type="primary"
			                       size="small"
			                       class="item-btn"
			                       @click="changeOrderType(item,'finish')">{{$L('确认收货')}}</el-button>
			
			
			
			            <el-button v-if="item.thisOrderStatus === 0"
			                       size="small"
			                       class="item-btn"
			                       @click="changeOrderType(item,'cancel')">{{$L('取消订单')}}</el-button>
			            <el-button
			                    v-if="item.thisOrderStatus < 0 || item.thisOrderStatus == 4 || item.thisOrderStatus == 5"
			                    @click="changeOrderType(item,'del')"
			                    class="item-btn"
			                    size="small">{{$L('删除订单')}}</el-button>
			
			            <el-button v-if="item.thisOrderStatus === 3 || item.thisOrderStatus === 4"
			                       size="small"
			                       class="item-btn"
			                       @click="changeOrderType(item,'afterSaleInfo')">{{$L('售后详情')}}</el-button>
			
			            <el-button type="text"
			                       class="item-btn"
			                       @click="changeOrderType(item,'info')">{{ $L('订单详情') }}></el-button>
			
			            <el-button type="text"
			                       class="item-btn"
			                       v-if="item.commentsStatus === 1"
			                       @click="changeOrderType(item,'evaluateInfo')">{{ $L('查看评价') }}></el-button>
			        </div>
			    </div>
			</div>
			<div class="bottom" v-if="link">
				<router-link :to="{name:'orderList',params:{type:all}}" class="mainFontColor">
					{{$L('查看更多')}}
				</router-link>
			</div>
		</div>
        <div class="list-no-data" v-else>
            {{ $L('暂无数据') }}
        </div>
    </div>
</template>

<script>
    import orderPay from "../../views/web/orderChildren/orderPay.vue";

    export default {
        name: "orderList",
        props: {
            orderList: {
                type: Array,
                default: () => {
                    return [];
                }
            },
			link:{
				type:String,
				default:''
			}
        },
        data() {
            return {
                loading: null
            }
        },
        methods: {
            statusFunc (status){
                let x=this.$L('待付款');
                switch(status){
                    case -1:
                        x = this.$L('货到付款');
                        break;
                    case -2:
                        x = this.$L('到店付款');
                        break;
                    case -3:
                        x = this.$L('等待汇款');
                        break;
                }
                return x;
            },
            changeOrderType(item, status) {
                switch (status) {
                    case 'del':
                        //删除订单
                        if (item.thisOrderStatus < 0 || item.thisOrderStatus == 4 || item.thisOrderStatus == 5) {
                            this.$confirm(this.$L('是否确认删除订单？'), this.$L('删除订单'), {
                                confirmButtonText: this.$L('立即删除'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('changeType', {
                                    orderId: item.orderId,
                                    orderType: 'del'
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        this.reloadData();
                                    } else {
                                        console.log(reData);
                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    console.log(error);
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'refund':
                        if(item.hasOwnProperty('canRefund') && item.canRefund === 1){
                            //申请售后
                            this.$router.push({name: "orderService", params: {id: item.orderId}});
                            break;
                        }
                        break;
                    case 'pay':
                        //立即付款
                        if ((item.thisOrderStatus === 0 || item.thisOrderStatus === 1) && item.payStatus >= 0) {
                            this.$store.commit('setDialogObj', {
                                name: this.$L('订单支付'),
                                view: orderPay,
                                width: '900px',
                                loading: true,
                                data:{
                                    orderId: item.orderId,

                                }
                            });
                        }
                        break;
                    case 'cancel':
                        //取消订单
                        if (item.thisOrderStatus === 0) {
                            this.$confirm(this.$L('确认取消订单吗？'), this.$L('取消订单'), {
                                confirmButtonText: this.$L('立即取消'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('changeType', {
                                    orderId: item.orderId,
                                    orderType: 'cancel'
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        this.reloadData();
                                    } else {
                                        console.log(reData);
                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    console.log(error);
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'finish':
                        //确认收货
                        if (item.thisOrderStatus === 2) {
                            this.$confirm(this.$L('是否确认收货？'), this.$L('确认收货'), {
                                confirmButtonText: this.$L('立即确认'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('changeType', {
                                    orderId: item.orderId,
                                    orderType: 'finish'
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        this.reloadData();
                                    } else {
                                        console.log(reData);
                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    console.log(error);
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'evaluate':
                        //去评价
                        if (item.commentsStatus === 0) {
                            this.$router.push({name: "orderComment", params: {id: item.orderId}});
                        }
                        break;
                    case 'afterSaleInfo':
                        //售后详情
                        if (item.thisOrderStatus === 3 || item.thisOrderStatus === 4) {
                            this.$router.push({name: "orderServiceInfo", params: {id: item.orderId}});
                        }
                        break;
                    case 'evaluateInfo':
                        //评价详情
                        if (item.commentsStatus === 1) {
                            this.$router.push({name: "checkComment", params: {id: item.orderId}});
                        }
                        break;
                    case 'info':
                        //订单详情
                        this.$router.push({name: "orderInfo", params: {id: item.orderId}});
                        break;
                }
            },
            gotoGoodsPage(item) {
                console.log(item);
            },
            showLoad(type) {
                if (typeof type === "undefined" || type) {
                    this.loading = this.$loading({lock: true});
                } else {
                    if (this.loading) {
                        this.loading.close();
                        this.loading = null;
                    }
                }
            },
            reloadData(){
                this.$emit("reloadData");
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/common/lib";

    .order-list {
        position: relative;
        width: 100%;
		.list-content{
			.list-main {
			    position: relative;
			    width: 100%;
			    padding: 0 0 15px 0;
			    margin-bottom: 20px;
			    background-color: #fff;
			
			    .list-msg {
			        position: relative;
			        width: 100%;
			        background: #fff;
			        font-size: 14px;
			        height: 50px;
			        line-height: 50px;
			        padding: 0 20px;
			
			        .order-time {
			            color: #999;
			            margin-left: 40px;
			        }
			    }
			
			    .list-head {
			        position: relative;
			        display: flex;
			        margin: 0 20px;
			        border: 1px solid #dcdfe6;
			        @extend .no-select;
			
			        .item {
			            height: 50px;
			            line-height: 50px;
			            font-size: 14px;
			            text-align: center;
			            background: #fff;
			            color: #666;
			            font-weight: 700;
			            padding: 0 20px;
			
			            &.item-one {
			                width: 13%;
			            }
			
			            &.item-two {
			                width: 45%;
			                text-align: left;
			            }
			
			            &.item-three {
			                font-size: 14px;
			                width: 16%;
			            }
			        }
			    }
			
			    .list-info {
			        position: relative;
			        background: #fff;
			        margin: 20px;
			
			        .item {
			            display: inline-block;
			            text-align: center;
			            vertical-align: top;
			
			            &.item-one {
			                width: 13%;
			                height: 100px;
			                font-size: 14px;
			
			                .item-one-txt {
			                    padding-top: 10px;
			                }
			
			                .price {
			                    height: 100px;
			                    margin-bottom: 20px;
			                    padding-top: 10px;
			                    font-weight: 600;
			
			                    &.font-price {
			                        color: #666;
			                        font-weight: 400;
			                    }
			
			                    .font-grey {
			                        color: #999;
			                        font-weight: 400;
			                    }
			                    .old-price{
			                        color: #999;
			                        text-decoration: line-through;
			                    }
			                }
			
			                .color-info {
			                    color: #909399;
			                }
			
			                .color-danger {
			                    color: #F56C6C;
			                }
			
			                .color-warning {
			                    color: #E6A23C;
			                }
			
			                .color-success {
			                    color: #67C23A;
			                }
			            }
			
			            &.item-two {
			                width: 45%;
			                text-align: left;
			            }
			
			            &.item-three {
			                width: 16%;
			                font-size: 14px;
			                padding: 10px 0;
			
			                .item-btn {
			                    display: block;
			                    margin: 0 auto 10px;
			                    display: block;
			                    width: 100px;
			                    line-height: 35px;
			                    padding: 0;
			
			                    &:last-child {
			                        margin-bottom: 0;
			                    }
			                }
			            }
			
			            .info {
			                position: relative;
			                width: 100%;
			                overflow: hidden;
			                padding-bottom: 20px;
			
			                &:last-child {
			                    padding-bottom: 0;
			                }
			
			                .list-main-left {
			                    width: 100px;
			                    height: 100px;
			                    float: left;
			
			                    .goods-img {
			                        width: 100px;
			                        height: 100px;
			                        text-align: center;
			                        line-height: 100px;
			                        cursor: pointer;
			                        &:hover{
			                            opacity: 0.8;
			                        }
			                        .error-img {
			                            font-size: 80px;
			                        }
									/deep/ .el-image__inner{
										width: inherit;
									}
			                    }
			                }
			
			                .list-main-right {
			                    padding-left: 18px;
			                    float: left;
			                    height: 100px;
			                    width: calc(100% - 120px);
			
			                    .list-main-right-item {
			                        cursor: default;
			                        line-height: 26px;
			
			                        &.gray {
			                            color: #909399;
			                        }
			                    }
			
			                    .orderInfo {
			                        @include diy-animation-show(color);
			                        cursor: pointer;
			                        white-space: nowrap;
			                        text-overflow: ellipsis;
			                        overflow: hidden;
			                        word-break: break-all;
			                        padding: 10px 0;
			                        font-size: 14px;
			                    }
			                }
			
			            }
			
			        }
			    }
			}
			.bottom{
				height: 70px;
				line-height: 70px;
				background-color: #FFFFFF;
				text-align: center;
				font-size: 14px;
			}
		}
        .list-no-data {
            @extend .no-select;
            position: relative;
            width: 100%;
            height: 380px;
            line-height: 380px;
            text-align: center;
            background-color: #ffffff;
            font-size: 16px;
            color: #999;
            margin-bottom: 20px;
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>
<style>
	.el-message-box {
		width: 420px !important;
	}
</style>